'use client';

import React from 'react';

interface ButtonProps {
  children: React.ReactNode;
  variant?: 'primary' | 'secondary' | 'success' | 'danger' | 'warning' | 'ghost';
  size?: 'sm' | 'md' | 'lg' | 'xl';
  onClick?: () => void;
  disabled?: boolean;
  loading?: boolean;
  icon?: string;
  className?: string;
  type?: 'button' | 'submit' | 'reset';
  fullWidth?: boolean;
}

const Button: React.FC<ButtonProps> = ({
  children,
  variant = 'primary',
  size = 'md',
  onClick,
  disabled = false,
  loading = false,
  icon,
  className = '',
  type = 'button',
  fullWidth = false
}) => {
  const baseClasses = `
    font-semibold rounded-2xl transition-all duration-300 
    transform hover:-translate-y-1 active:translate-y-0
    flex items-center justify-center gap-3 
    shadow-lg hover:shadow-xl active:shadow-md
    focus:outline-none focus:ring-4 focus:ring-opacity-50
    disabled:opacity-50 disabled:cursor-not-allowed 
    disabled:transform-none disabled:shadow-lg
    relative overflow-hidden group
  `;
  
  const variants = {
    primary: `
      bg-gradient-to-r from-purple-600 to-pink-600 
      text-white hover:from-purple-700 hover:to-pink-700
      focus:ring-purple-300 shadow-purple-200
    `,
    secondary: `
      bg-white text-purple-600 hover:bg-purple-50 
      border-2 border-purple-600 hover:border-purple-700
      focus:ring-purple-300 shadow-purple-100
    `,
    success: `
      bg-gradient-to-r from-green-500 to-emerald-600 
      text-white hover:from-green-600 hover:to-emerald-700
      focus:ring-green-300 shadow-green-200
    `,
    danger: `
      bg-gradient-to-r from-red-500 to-rose-600 
      text-white hover:from-red-600 hover:to-rose-700
      focus:ring-red-300 shadow-red-200
    `,
    warning: `
      bg-gradient-to-r from-yellow-500 to-orange-500 
      text-white hover:from-yellow-600 hover:to-orange-600
      focus:ring-yellow-300 shadow-yellow-200
    `,
    ghost: `
      bg-transparent text-gray-600 hover:bg-gray-100 
      border border-gray-300 hover:border-gray-400
      focus:ring-gray-300 shadow-gray-100
    `
  };
  
  const sizes = {
    sm: 'px-4 py-2 text-sm min-h-[2.5rem]',
    md: 'px-6 py-3 text-base min-h-[3rem]',
    lg: 'px-8 py-4 text-lg min-h-[3.5rem]',
    xl: 'px-10 py-5 text-xl min-h-[4rem]'
  };

  const widthClass = fullWidth ? 'w-full' : '';

  return (
    <button
      type={type}
      onClick={onClick}
      disabled={disabled || loading}
      className={`
        ${baseClasses} 
        ${variants[variant]} 
        ${sizes[size]} 
        ${widthClass}
        ${className}
      `}
    >
      {/* Shimmer Effect */}
      <div className="absolute inset-0 -top-full bg-gradient-to-b from-transparent via-white/20 to-transparent group-hover:top-full transition-all duration-700"></div>
      
      {/* Loading Spinner */}
      {loading && (
        <div className="absolute inset-0 flex items-center justify-center">
          <div className="w-5 h-5 border-2 border-current border-t-transparent rounded-full animate-spin"></div>
        </div>
      )}
      
      {/* Content */}
      <div className={`flex items-center gap-3 ${loading ? 'opacity-0' : 'opacity-100'} transition-opacity`}>
        {icon && (
          <span className="text-xl group-hover:scale-110 transition-transform duration-300">
            {icon}
          </span>
        )}
        <span className="relative z-10">{children}</span>
      </div>
    </button>
  );
};

export default Button;
